import React, {useState, useEffect, useRef} from 'react'
import classes from './styles/MainWindow.module.css'
import MessageList from './messageList'
import axios from 'axios'
import unnamed from './unnamed.jpg'

const MainWindow = ({companion}) => {
    const textareaRef = useRef(null);
    const [messageText, setMessageText] = useState('')

    const sendMessage = (e) => {
        e.preventDefault()
        console.log(messageText)
        setMessageText('');
        setMessages([{"from": true, "text": messageText, "time": timeNow()}, ...messages])
    }

    const timeNow = () => {
        let time = new Date
        return time.toTimeString()
    }
    const [messages, setMessages] = useState([
        {"from": true, "text": "privet", "time": "10:50"},
        {"from": false, "text": "zdarova bratela", "time": "10:51"},
        {"from": true, "text": "hochu kushat'", "time": "10:50"},
        {"from": false, "text": "pizzu idem kushat'?", "time": "10:50"},
        {"from": true, "text": "da", "time": "10:50"},
        {"from": false, "text": "pizzu idem kushat'?", "time": "10:50"}
        // {"from": true, "text": "da", "time": "10:50"},
        // {"from": false, "text": "pizzu idem kushat'?", "time": "10:50"},
        // {"from": true, "text": "da", "time": "10:50"}
    ])

    /* Dev test items */
    useEffect(() => {
        axios.get('/talk1').then(res => setMessages(res.data)).catch(error => console.log(error))
    }, [])

    /* Dev test items */
    useEffect(()=> {
        textareaRef.current.style.height = "0px";
        const scrollHeight = textareaRef.current.scrollHeight;
        textareaRef.current.style.height = scrollHeight + "px";
    }, [messageText])

    return (
        <div className={classes.dialogWindow}> 
            <div className={classes.companion}>
                {companion.photoURL === undefined
                ? <div className={classes.avatar}><img src={unnamed}/></div>
                : <div className={classes.avatar}><img src={companion.photoURL}/></div>
                }
                <h2>{companion.name}</h2>
            </div>
            <MessageList messages={messages}/>
            <form className={classes.txtArea}>
            <div className={classes.wInputContainer}>
                    <textarea ref={textareaRef} className={classes.wrapper} placeholder="Type a message" value={messageText} onChange={(e) => (setMessageText(e.target.value))}></textarea>
            </div>
            <button onClick={sendMessage}>Send</button>
            </form>
        </div>
    )
}

export default MainWindow;