import React, {useState, useEffect} from 'react'
import MessageListItem from './MessageListItem'
import classes from './styles/MessageList.module.css'

const MessageList = ({messages}) => {

    /* Dev test items */
    /* Dev test items */

    return (
        <div className={classes.MessageList}>
            {messages ?
            messages.map((msgItem, index) => 
                <MessageListItem from={msgItem.from} text={msgItem.text} time={msgItem.time} key={index}/>
            )
            :<MessageListItem from={true} text={'Напишите этому человеку!'} time={'А это не важно'}/>}
        </div>
    )
}

export default MessageList
