import React, {useState} from 'react'
import classes from './styles/Search.module.css'
import axios from 'axios'
import unnamed from './unnamed.jpg'

const Search = () => {
    const [searchInput, setSearchInput] = useState('')
    const [names, setNames] = useState([])
    const [activeInput, setInputActive] = useState(false)

    const search = (e) => {
        if(e.target.value.length !== 0)axios({
            method: 'get',
            url: '/search_user',
            params: {
                value: e.target.value
            }
        })
        .then(res => {
            setNames(res.data.users)
        })
        .catch(error => console.log(error))
        else setNames([])
        setSearchInput(e.target.value)
    }

    return (
        <div className={classes.searchBox}>
            <input className={classes.searchInput} onFocus={() => (setInputActive(true))} onBlur={() => (setInputActive(false))} placeholder='Write your talk here' value={searchInput} onChange={search}></input>
            <div className={classes.dropDownList} style={{display: searchInput.length && activeInput ? 'flex' : 'none'}}>
                <ul>
                    {names.length ? names.map( (name, index) => (
                        <SearchItem srcPath={name?.photoURL ? name.photoURL : unnamed} name={name.name}/>
                    )) : <li className={classes.searchItem}>Пользователь не найден</li>}
                </ul>
            </div>
        </div>
    )
}

export default Search

const SearchItem = ({srcPath, name}) => {
    return (
    <li className={classes.searchItem}>
        <img height='30' width='30' style={{borderRadius: '75%'}} src={srcPath}/> <p>{name}</p>
    </li>
    )
}
