import React, {useState, useEffect} from 'react';
import axios from 'axios'
import './App.css';
import {Route, Switch, Redirect, BrowserRouter} from 'react-router-dom'
import Messenger from './messenger';
import Loader from './components/Loader';
import WelcomePage from './components/WelcomePage';

function App() {
  // const [currentDialog, setCurrentDialog] = useState(0)
  const [user, setUser] = useState('loading')
  const [flashMsg, setFlashMsg] = useState('')
  

  /* Dev Test Time */
  const dialogs = [
    {id: 1, name: 'Coming soon!', photoURL: undefined, talks: [{id: 1, name:'Coming soon!'}, {id:2, name: 'Coming soon!'}, {id: 3, name:'Coming soon!'}]}, 
    {id: 2, name: 'Coming soon!', photoURL: undefined, talks: [{id: 1, name:'Coming soon!'}, {id: 2, name: 'Coming soon!'}]},{id: 2, name: 'Coming soon!', photoURL: undefined, talks: [{id: 1, name:'Coming soon!'}, {id: 2, name: 'Coming soon!'}]},{id: 2, name: 'Coming soon!', photoURL: undefined, talks: [{id: 1, name:'Coming soon!'}, {id: 2, name: 'Coming soon!'}]},{id: 2, name: 'Coming soon!', photoURL: undefined, talks: [{id: 1, name:'Coming soon!'}, {id: 2, name: 'Coming soon!'}]},{id: 2, name: 'Coming soon!', photoURL: undefined, talks: [{id: 1, name:'Coming soon!'}, {id: 2, name: 'Coming soon!'}]},{id: 2, name: 'Coming soon!', photoURL: undefined, talks: [{id: 1, name:'Coming soon!'}, {id: 2, name: 'Coming soon!'}]},{id: 2, name: 'Coming soon!', photoURL: undefined, talks: [{id: 1, name:'Coming soon!'}, {id: 2, name: 'Coming soon!'}]},{id: 2, name: 'Coming soon!', photoURL: undefined, talks: [{id: 1, name:'Coming soon!'}, {id: 2, name: 'Coming soon!'}]}
]
  /* Dev Test Time */
    useEffect(() => {
      axios({
        method: 'get',
        url: '/is_authorized',
      }).then(res => {
          if(res.data.status == 0){
              setUser(res.data.is_auth)
              console.log(res.data)
          }
      }).catch(error => console.log(error))
    }, [])
  if(user === 'loading'){
    return (<div style={{height: '100vh',
                        backgroundColor: 'black'}}><Loader/></div>)
  } else return (
    <BrowserRouter>
      {user ? 
      (
        <Switch>
          <Route key={'/talk'} exact={true} path={'/talk'} component={() =>( <Messenger dialogs={dialogs} setLoggedOut={setUser}/>)} />
           <Redirect to={'/talk'} />
        </Switch>
      )
      :
      (
        <Switch>
          <WelcomePage setUser={setUser} />
        </Switch>
      )}
    </BrowserRouter>
  );
}

export default App;
